angular
  .module('app', ['ui.router', 'ui.materialize']);

angular.module('app')

  .service('userService', function ($q, $http, AuthTool, API_ENDPOINT) {
    var self = this;
    var user;

    self.getUser = function () {
      if (user) {
        return $q.resolve(user);
      }
      else {
        if (AuthTool.getToken()) {
          return $http.get(API_ENDPOINT.url + '/memberInfo').then(function (data) {
            user = data.data.user;
            return data.data.user;
          });
        }
        else {
          return $q.resolve(user);
        }
      }
    };

    self.deleteUser = function() {
      user = null;
    };

  });

angular.module('app')

  .service('AuthTool', function ($window) {
    var self = this;

    self.saveToken = function (token) {
      $window.localStorage["jwttoken"] = token;
    };

    self.getToken = function () {
      return $window.localStorage["jwttoken"];
    };
  })

  .service('AuthService', function ($window, $q, $http, API_ENDPOINT, AuthTool, userService) {
    var self = this;

    self.register = function (user) {
      return $http.post(API_ENDPOINT.url + '/signup', user);
    };

    self.login = function (user) {
      return $http.post(API_ENDPOINT.url + '/authenticate', user);
    };

    self.getUserInfo = function () {
      return $http.get(API_ENDPOINT.url + '/memberInfo');
    };

    self.logout = function () {
      return $q.resolve($window.localStorage.removeItem('jwttoken')).then(function() {
        userService.deleteUser();
      });
    };

    self.parseJwt = function (token) {
      var base64Url = token.split('.')[1];
      var base64 = base64Url.replace('-', '+').replace('_', '/');
      return JSON.parse($window.atob(base64));
    };

    self.isAuthed = function () {
      var token = AuthTool.getToken();

      return token;
    };
  })

  .factory('AuthInterceptor', function ($q, $window, AuthTool, API_ENDPOINT) {
    return {
      response: function (response) {
        if (response.config.url.indexOf(API_ENDPOINT.url) === 0 && response.data.token) {
          AuthTool.saveToken(response.data.token);
        }

        return response;
      },
      request: function (config) {
        var token = AuthTool.getToken();
        if (config.url.indexOf(API_ENDPOINT.url) === 0 && token) {
          config.headers.Authorization = token;
        }

        return config;
      },
      responseError: function (response) {
        if (response.status === 401) {
          $window.open('/', '_self');
        }

        return $q.reject(response);
      }
    };
  })

  .config(function ($httpProvider) {
    $httpProvider.interceptors.push('AuthInterceptor');
  });

angular.module('app')
  .controller('newGroupCtrl', function ($http, API_ENDPOINT) {
    var vm = this;

    $http.get(API_ENDPOINT.url + '/memberInfo').then(function (data) {
      vm.user = data.data.user;
    });

    vm.createGroup = function() {
      console.log(vm.group);
      $http.post(API_ENDPOINT.url + '/users/' + vm.user._id + '/groups', vm.group).then(function(data) {
        console.log(data);
      });
    };
  });

angular
  .module('app')
  .component('gptMenu', {
    templateUrl: 'app/menu/menu.html',
    controllerAs: 'vm',
    controller: function ($window, AuthService, userService) {
      var vm = this;

      vm.isAuthed = AuthService.isAuthed();

      userService.getUser()
        .then(function (user) {
          vm.user = user;
        });

      vm.logout = function () {
        AuthService.logout().then(function () {
          $window.open('/', '_self');
        });
      };
    }
  });

angular
  .module('app')
  .controller('inscriptionCtrl', function ($state, $window, AuthService) {
    var vm = this;

    vm.signup = function () {
      AuthService.signup(vm.user)
        .then(function (data) {
          if (data.data.success) {
            AuthService.login(vm.user)
              .then(function (data) {
                if (data.data.success) {
                  $window.open('/inside', '_self');
                }
                else {
                  vm.errorMessage = data.data.msg;
                }
              })
              .catch(function (err) {
                console.log(err);
              });
          }
          else {
            vm.errorMessage = data.data.msg;
          }
        })
        .catch(function (err) {
          console.log(err);
        });
    };
  });

